<template>
  <div class="py-5">
    <h1>Выбор балкона</h1>
    <div class="row mt-5">

      <div class="col-md-6 col-sm-12 mb-3"
            v-for="balcony in balconies" :key="balcony.id">
        <div class="card">
          <img :src="balcony.overview" class="card-img-top" style="max-height: 200px">
          <div class="card-body">
            <h5 class="card-title">{{balcony.title}}</h5>
            <router-link :to="'/config/' + balcony.id" class="btn btn-primary">Выбрать</router-link>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import UserServices from '../services/User'

export default {
  name: 'Balconies',
  data() {
    return {
      balconies: []
    }
  },
  methods: {
    async getBalconies() {
      const balconies = await UserServices.getBalconies()
      this.balconies = balconies
    }
  },
  mounted() {
    this.getBalconies()
  }
}
</script>
